  .w-col-small-9{width:75%}
  .w-col-small-10{width:83.33333333%}
  .w-col-small-11{width:91.66666667%}
  .w-col-small-12{width:100%}
}

@media screen and (max-width:479px){
  .w-container{max-width:none}
